body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.news-card{
    color: black;
    text-decoration: none;
    cursor: pointer;
}
.new-card__body-label,
.news-card__header{
    margin-bottom: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.news-card__header--new{
    color: green;
}
.transactions-card{
    color: black;
    text-decoration: none;
}
.transactions-card__header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.transactions-card__value--positive{
    color: green;
}
.transactions-card__value--positive:before{
    content: '+ ';
}
.transactions-card__value--negative{
    color: red;
}
.transactions-card__value--negative:before{
    content: '- ';
}
.transactions-card__footer{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.time-line{
    height: auto;
}
.time-line__setting{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 30px;
    background: white;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.time-line__setting-label{
    margin: 5px 10px;
}
.time-line__cards{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.time-line__card{
    padding: 20px;
    width: 215px;
    border: solid 1px black;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 10px auto;
}
