.add-event{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 30px;
}
.add-event__headline{
    margin-top: 0;
}
.add-event__toggle{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 20px;
}
.add-event__label{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 10px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.add-event__label-title{
    display: -webkit-flex;
    display: flex;
    min-width: 38%;
    margin-right: 5px;
    margin-bottom: 5px;
}
.add-event__input{
    max-width: 62%;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    min-width: 170px;
    margin-bottom: 5px;
}
.add-event__input--textarea{
    resize: vertical;
}
.add-event__action{
    margin: 20px auto;
}
.news-card--info{
    margin: 30px;
}
.news-card__body-description{
    word-wrap: break-word;
}

.transactions-card--info{
    color: black;
    text-decoration: none;
    cursor: pointer;
    margin: 30px;
}
.transactions-card__subtitle,
.transactions-card__body-label,
.transactions-card__header{
    margin-bottom: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.mini-navigator{
    display: none;
}
@media  (max-width: 599px) {
    .mini-navigator{
        display: -webkit-flex;
        display: flex;
        width: 100vw;
        height: 10vh;
        background: cadetblue;
        position: fixed;
        bottom: 0;
        left: 0;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-align-items: center;
                align-items: center;
    }
}
.app {
  display: -webkit-flex;
  display: flex;
  min-width: 320px;
}
.app__add-and-info{
  -webkit-flex-grow: 1;
          flex-grow: 1;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.app__info{
  position: -webkit-sticky;
  position: sticky;
}
