.transactions-card{
    color: black;
    text-decoration: none;
}
.transactions-card__header{
    display: flex;
    justify-content: space-between;
}
.transactions-card__value--positive{
    color: green;
}
.transactions-card__value--positive:before{
    content: '+ ';
}
.transactions-card__value--negative{
    color: red;
}
.transactions-card__value--negative:before{
    content: '- ';
}
.transactions-card__footer{
    display: flex;
    justify-content: space-between;
}